<template>
    <div>
        <button @click="testFunc">
            点我
        </button>
    </div>
</template>

<script>

export default {
    name: 'TestPage',
    data(){

    },
    
    mounted(){
        this.testFunc();
    },

    methods:{
        async testFunc(){
            try {
                const response = await fetch('https://raw.githubusercontent.com/3542Q/JSRepository/main/HelloJS.js')
                const code = await response.text()
                console.log(code)
                const module = new Function(code + '; return { sayHello }')()
                module.sayHello()
            } catch (error) {
                console.error('Failed to load script:', error)
            }
        }
    },
}

</script>

